"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { api } from "@/lib/api";

export default function SentimentPage() {
    const [text, setText] = useState("");
    const [result, setResult] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const router = useRouter();

    useEffect(() => {
        const token = localStorage.getItem("token");
        if (!token) {
            router.push("/login");
        }
    }, [router]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        setError("");
        setResult(null);

        try {
            // Using /predict as per backend implementation
            const response = await api.post("/predict", { text });
            setResult(response.data);
        } catch (err) {
            if (err.response) {
                if (err.response.status === 401) {
                    setError("Session expired. Please login again.");
                    localStorage.removeItem("token");
                    setTimeout(() => router.push("/login"), 2000);
                } else {
                    setError(err.response.data.detail || "An error occurred.");
                }
            } else {
                setError("Network error. Please try again.");
            }
        } finally {
            setLoading(false);
        }
    };

    const getSentimentColor = (sentiment) => {
        switch (sentiment?.toLowerCase()) {
            case "positive":
                return "text-green-400";
            case "negative":
                return "text-red-400";
            case "neutral":
                return "text-gray-400";
            default:
                return "text-white";
        }
    };

    return (
        <div className="min-h-screen flex flex-col items-center justify-center bg-gray-900 text-white p-4">
            <div className="bg-gray-800 p-8 rounded-lg shadow-lg w-full max-w-2xl">
                <h1 className="text-3xl font-bold mb-6 text-center text-purple-400">Sentiment Analysis</h1>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium mb-2">Enter Text</label>
                        <textarea
                            value={text}
                            onChange={(e) => setText(e.target.value)}
                            className="w-full p-3 rounded bg-gray-700 border border-gray-600 focus:border-purple-400 focus:outline-none h-32 resize-none"
                            placeholder="Type something here..."
                            required
                        />
                    </div>

                    {error && <p className="text-red-400 text-sm text-center">{error}</p>}

                    <button
                        type="submit"
                        disabled={loading}
                        className="w-full bg-purple-500 hover:bg-purple-600 text-white font-bold py-2 px-4 rounded transition duration-200 disabled:opacity-50"
                    >
                        {loading ? "Analyzing..." : "Analyze Sentiment"}
                    </button>
                </form>

                {result && (
                    <div className="mt-8 p-4 bg-gray-700 rounded border border-gray-600 animate-fade-in">
                        <h2 className="text-xl font-semibold mb-2 text-center">Result</h2>
                        <div className="flex justify-between items-center">
                            <span className="text-gray-300">Sentiment:</span>
                            <span className={`text-2xl font-bold ${getSentimentColor(result.sentiment)}`}>
                                {result.sentiment}
                            </span>
                        </div>
                        <div className="flex justify-between items-center mt-2">
                            <span className="text-gray-300">Score:</span>
                            <span className="text-xl font-mono">{result.score.toFixed(4)}</span>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
